<template>
  <div id="timer_container">
    <span class="time" id="timer">{{ time }}</span>
    <button class="timer" @click="addTime(72, 2)">
      <h2>Phase 2</h2>
      <h3>72 seconds</h3>
    </button>
    <button class="timer" @click="addTime(60, 3)">
      <h2>Phase 3</h2>
      <h3>60-72 seconds</h3>
    </button>
    <button class="timer" @click="addTime(30, 4)">
      <h2>Phase 4</h2>
      <h3>30 seconds</h3>
    </button>
  </div>
</template>

<script>
import { ref, onMounted } from "vue";
export default {
  name: "Timer",
  setup() {
    const time = ref(0);
    let phase = 0;
    onMounted(() => {
      setInterval(() => {
        if (time.value > 0) {
          time.value--;
        } else if (time.value == 0 && phase == 2) {
          time.value = 72;
        } else if (time.value == 0 && phase == 3) {
          time.value = 60;
        } else if (time.value == 0 && phase == 4) {
          time.value = 30;
        } else {
          clearInterval();
        }
      }, 1000);
    });
    function addTime(addtime, changephase) {
      phase = changephase;
      time.value = addtime;
      clearInterval();
    }
    return {
      time,
      addTime
    };
  }
};
</script>

